
/* General Styles */
header {
    display: flex;
    justify-content: space-between;
    padding: 1rem 2rem;
}

.nav ul {
    list-style: none;
    display: flex;
    gap: 1rem;
}

.nav a {
    text-decoration: none;
    color: #fff;
    font-weight: bold;
    transition: color 0.3s;
}

.nav a:hover {
    color: #941874;
}

.hero {
    text-align: center;
    padding: 4rem 2rem;
}

.projects .project-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 1rem;
}

button {
    background: #941874;
    color: #fff;
    border-radius: 0.5rem;
    padding: 0.3rem 1rem;
    cursor: pointer;
    transition: background 0.3s;

}

button:hover {
    background: #711de0;
}




/* General Reset */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/* Body Styling */
body {
    font-family: 'Arial', sans-serif;
    line-height: 1.6;
    background-color: #f9f9f9;
    color: #333;
    overflow-x: hidden;
}

/* Header */
header {
    background: linear-gradient(to right, #38065f, #711de0);
    color: #fff;
    padding: 1rem 2rem;
    position: sticky;
    top: 0;
    z-index: 1000;
    display: flex;
    justify-content: space-between;
    align-items: center;
    animation: fadeInDown 1s ease-in-out;
}

header .logo {
    font-size: 1.8rem;
    font-weight: bold;
    color: #fff;
    text-transform: uppercase;
}

header nav {
    display: flex;
    gap: 1.5rem;
}

header nav a {
    color: #fff;
    text-decoration: none;
    font-weight: bold;
    transition: transform 0.3s ease-in-out;
    padding: 0.5rem 1rem;
    border-radius: 5px;
}

header nav a:hover {
    transform: scale(1.1);
    background-color: #711de0;
}

/* Toggle Menu */
.menu-toggle {
    display: none;
    flex-direction: column;
    cursor: pointer;
}

.menu-toggle span {
    width: 30px;
    height: 3px;
    background: #fff;
    margin: 5px;
    transition: 0.3s ease-in-out;
}

/* Mobile Menu */
.mobile-nav {
    display: none;
    flex-direction: column;
    align-items: center;
    background: #38065f;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    padding: 1rem 0;
}

.mobile-nav a {
    margin: 0.5rem 0;
    color: #fff;
}

/* Hero Section */
.hero {
    text-align: center;
    padding: 5rem 2rem;
    background: linear-gradient(to bottom, #941874, #ffffff);
    color: #fff;
    animation: slideIn 1.5s ease-in-out;
}

.hero h1 {
    font-size: 3rem;
    margin-bottom: 1rem;
    animation: fadeIn 2s ease-in;
}

.hero p {
    font-size: 1.2rem;
}

/* Responsive Design */
@media screen and (max-width: 768px) {
    nav {
        display: none;
    }

    .menu-toggle {
        display: flex;
    }

    .mobile-nav {
        display: flex;
    }
}
